$border: rgb(227, 237, 238);
$lightBlue: rgb(204, 237, 243);
$yellow: #ffb92e;

.search {
  &-live-list {
    position: absolute;
    width: 100%;
    background-color: #fff;
    overflow: hidden;
    z-index: 2;

    &-item {
      display: block;
      color: #000;
      padding: 8px 20px;
      text-decoration: none;

      img {
        width: 15px;
        margin-right: 10px;
      }
      &.results-not-found {
        cursor: default;
      }
    }
    li {
      text-align: left;
      cursor: pointer;

      &:hover {
        background-color: $border;
      }
    }
  }
  &-form {
    position: relative;
    text-align: center;
    background-color: #fff;
    border-radius: 20px;
    display: block;
    z-index: 2;
    max-width: 630px;
    width: 100%;
    margin: 8vh auto 60px auto;

    .search-labels {
      border-radius: 20px;
      display: flex;
      height: 50px;
      padding: 0;
      overflow: hidden;
      background-color: transparent;

      &-input {
        height: 100%;
        background-color: transparent;
        width: 100vw;
        max-width: 500px;
        padding: 0px 20px;
        z-index: 2;
        outline: none;
        border: none;
        position: relative;
      }

      &-button {
        margin-left: -20px;
        width: 120px;
        height: 100%;
        z-index: 1;
        background-color: $lightBlue;
        border: none;
        outline: none;
        cursor: pointer;
      }
    }
    .search-status {
      position: absolute;
      left: 10px;
      bottom: -30px;
      z-index: 0;
      font-size: 13px;
      color: rgb(233, 233, 233);
    }
  }
  &-form-live {
    border-radius: 20px 20px 0px 0px;
    .search-labels {
      border-radius: 20px 20px 0px 0px;
    }
    .search-live-list {
      border-radius: 0px 0px 20px 20px;
    }
  }
  //
  .result {
    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      &-item {
        margin: 7px 5px;
        width: 140px;
        background-color: #fff;
        border-radius: 3px;
        text-align: center;
        transition: all 0.05s ease;
        z-index: 0;
        border: none;
        outline: none;
        color: #000;
        font-size: 14px;
        cursor: pointer;

        &:hover {
          transform: scale(1.07);
        }

        img {
          width: 100%;
          height: 180px;
        }
        p {
          word-wrap: break-word;
          margin: 15px 5px 10px 5px;
        }
      }
      .preloader {
        margin-top: 50px;
        transform: scale(0.7);
      }
    }
    .more-books {
      margin: 30px 0px 20px 0px;
      display: flex;
      justify-content: center;

      &-btn {
        background-color: $yellow;
        padding: 5px 10px;
        box-sizing: border-box;
        border: none;
        outline: none;
        border-radius: 8px;
        font-size: 13px;
        margin: 0 auto;
        display: block;
      }
      .preloader {
        transform: scale(0.4);
        margin-top: -70px;
      }
    }
  }
}

.dn {
  display: none !important;
}

@media screen and (max-width: 500px) {
  .search {
    .result {
      &-book {
        margin: 7px 7px;
        width: 110px;
        p {
          font-size: 13px;
          margin: 10px 5px 5px 5px;
        }
        img {
          height: 140px;
        }
      }
    }
  }
} ;
